import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'

import {
  Grid, Typography, TableCell,
  TableRow
} from '@material-ui/core'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import SearchTextComponent from '../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import AgingSummaryBar from '../../Components/AgingSummaryBar/AgingSummaryBar'

import ListView from './components/ListView'
import CreateOptions from './components/CreateOptions'

import AgingSummaryServices from '../../../../../Services/Api/AgingSummaryServices'
import CollectionSuspensionServices from 'Services/Api/CollectionSuspensionServices'
import CollectionClientContractServices from 'Services/Api/CollectionClientContractServices'
import CollectionCDFServices from 'Services/Api/CollectionCDFServices'
import CollectionPenaltyServices from 'Services/Api/CollectionPenaltyServices'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { PageTitleService } from 'SharedComponents/PageTitleComponent/PageTitleComponent'

import style from './AgingSummaryClientPage.module.scss'

import { ViewAllListConfig } from './config/config.js'

export default function AgingSummaryClientPage(props) {
  const { accountNumber = '7409-5462-2115' } = props.match.params
  const history = useHistory()

  const initialValue = {
    body: [],
    count: 0,
    filters: {},
    header: [],
    limit: 10,
    order: true,
    orderBy: 'soa_date',
    page: 1,
    sort: '',
  }

  const initalSummaryBarValue = {
    total_collectibles: 0,
    ageSummary: [
      { name: "1 - 45 Days", total_amount_due:  0 },
      { name: "46 - 75 Days", total_amount_due: 0 }, 
      { name: "76 - 105 Days", total_amount_due: 0 }, 
      { name: "106 - 136 Days", total_amount_due: 0 }, 
      { name: "137 - 166 Days", total_amount_due: 0 }, 
      { name: "167 - 196 Days", total_amount_due: 0 }, 
      { name: "Over 197 Days", total_amount_due: 0 }, 
    ]
  }

  const [soaList, setSoaList] = useState(initialValue)
  const [selectedSoa, setSelectedSoa] = useState({})
  const [summaryBarData, setSummaryBarData] = useState(initalSummaryBarValue)
  const [params, setParams] = useState({})

  const onListViewFilterChange = () => {

  }

  const handlePregenerateSuspensionEndorsement = () => {
    if (selectedSoa.length === 0) {
      return
    }

    LoadingService.loading(true)

    CollectionSuspensionServices.store({ accountNumber, soas: Object.keys(selectedSoa) })
      .then(response => {
        LoadingService.loading(false)
        history.push(`/collection/suspension/endorse/client/${response.data.data}`)
      })
      .catch(error => {
        LoadingService.loading(false)
        console.error(error)
      })
  }

  const handlePregeneratePenaltyEndorsement = () => {
    if (selectedSoa.length === 0) {
      return
    }

    LoadingService.loading(true)

    CollectionPenaltyServices.store({ accountNumber, soa: Object.keys(selectedSoa) })
      .then(response => {
        LoadingService.loading(false)
        history.push(`/collection/penalty/client/${response.data.data}`)
      })
      .catch(error => {
        LoadingService.loading(false)
        console.error(error)
      })
  }

  const handlePregenerateCDF = () => {
    LoadingService.loading(true)
    const _soa = []
    Object.keys(selectedSoa).map(i => {
      if (selectedSoa[i]) {
        _soa.push(i)
      }
    })
    // Will not proceed if there's no soa selected
    if (_soa.length === 0) {
      LoadingService.loading(false)
      return false
    }

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: _soa } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const handleInlinePregenerateCDF = params => {
    LoadingService.loading(true)

    CollectionCDFServices.pregenerateCDF({ data: { accountNumber, soa: [params] } })
      .then(res => {
        LoadingService.loading(false)
        const { cdfNumber } = res.data.data
        history.push(`/collection/collection-distribution/client/${cdfNumber}`)
      })
      .catch(err => {
        LoadingService.loading(false)
        console.error(err)
      })
  }

  const onCheckboxChanged = params => {
    const { soa_number, value } = params
    setSelectedSoa(i => ({ ...i, [soa_number]: value }))
  }

  useEffect(() => {
    LoadingService.loading(true)
    Promise.all([
      AgingSummaryServices.getClientSOAList({ accountNumber }),
      CollectionClientContractServices.getClientContract({ accountNumber }),
      AgingSummaryServices.getSummaryBarData({ accountNumber, type: 'client' })])
      .then(res => {
        const [data, contract, summary] = res
        let { results, count } = data.data.data
        const { client } = contract.data.data
        const summaryData = summary.data.data

        results = results.length > 0 ? results : []
        count = count > 0 ? count : 0

        PageTitleService(`${client.account_name || ''} - Aging Summary`)

        BreadcrumbsService.setSelectedItems(['listOfAccountsWithAgingSummary', `clientAgingSummary:${client.account_name || ''}`])

        setSoaList(i => ({ ...i, body: results, count }))

        setSummaryBarData(summaryData)

        LoadingService.loading(false)
      })
      .catch(error => {
        console.error(`Error Fetching SOA(s): ${error}`)
      })

  }, [accountNumber])

  return (
    <Grid container spacing={4}>
      <Grid item xs={12}>
        <Grid container spacing={3}>
          <Grid item xs={12}>
            <SearchTextComponent
              placeholder="Search List of Accounts with Aging Summary"
              exportFiles={true}
              displaySettings={true}
            />
          </Grid>
          <Grid item xs={12} >
            <Grid container>
              <Grid item xs={12}>
                <AgingSummaryBar summaryBarData={summaryBarData} />
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={12} >
            <Grid container>
              <Grid item xs={12}>
                <CreateOptions 
                handlePregenerateCDF={handlePregenerateCDF} 
                handlePregenerateSuspensionEndorsement={handlePregenerateSuspensionEndorsement}
                handlePregeneratePenaltyEndorsement={handlePregeneratePenaltyEndorsement} />
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid item xs={12}>
        <Grid container>
          <Grid item xs={12} style={{ width: 'fit-content', overflow: 'auto', boxShadow: "1px 2px 5px #ddd" }}>
            <ListView
              id={'audit-log-view-all-list'}
              config={ViewAllListConfig}
              contents={soaList.body}
              count={soaList.count}
              list={soaList}
              onCheckboxChanged={onCheckboxChanged}
              // fetchItemDetails={fetchItemDetails}
              onListViewFilterChange={onListViewFilterChange}
              handlePregenerateCDF={handlePregenerateCDF}
              handleInlinePregenerateCDF={handleInlinePregenerateCDF}
              params={params}
            />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
