import React from 'react'
import { useHistory } from 'react-router-dom'
import Chart from 'react-apexcharts'
import Typography from '@material-ui/core/Typography'

// Components
import DashboardTile from './../DashboardTileComponent/DashboardTileComponent'

// Services
import AgingSummaryServices from '../../../../../Services/Api/AgingSummaryServices'

// Config
const filterMenu = {
  data: [
    { value: 'today', text: 'Today' },
    { value: 'this week', text: 'This Week' },
    { value: 'this month', text: 'This Month' }
  ],
  default: 'today'
}

// Mock-Data
const mock = require('./mock-data/aging-summary-graph.json')

const AgingSummaryTile = props => {
  
  const history = useHistory()
  const [chart, setChart] = React.useState({
    chart: {
      toolbar: {
        show: false
      },
      type: 'bar',
      height: 320
    },
    legend: {
      position: 'top'
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '40%'
      }
    },
    dataLabels: {
      enabled: false
    },
    stroke: {
      show: true,
      width: 2,
      colors: ['transparent']
    },
    xaxis: {
      categories: [' ']
    },
    fill: {
      opacity: 1
    },
    tooltip: {
      y: {
        formatter: function (val) {
          return `${val}M`
        }
      }
    },
    series: []
  })

  const changeTotalAmountDueName = (data) => {
    return data.map(item=>{
      return {
        name: item.name,
        data: [item.total_amount_due]
      }
    })
  } 

  React.useEffect(() => {
    // TODO: Change when end-point is created
    AgingSummaryServices.getSummaryBarData({ type: 'account' })
      .then(results=>{
        const { ageSummary } = results.data.data
        
        setChart(i => ({ ...i, series: changeTotalAmountDueName(ageSummary) }))
      })

    
  }, [])

  // Methods
  /**
   * Handle View All Link Behavior
   * @param route Link to where the page will redirect
   */
  const handleOnViewAll = () => {
    history.push('/collection/agingsummary/accounts')
  }

  return (
    <React.Fragment>
      <DashboardTile
        id='aging-summary-tile'
        count={{ color: 20, value: 'Aging Summary', id: 'aging-summary-tile-count' }}
        sizes={{ sm: 12 }}
        title=''
        isFilterShown
        filterMenu={filterMenu}
        viewList={
          <Typography
            id='aging-summary-tile-view-list'
            variant='caption'
            className='bold text-link'
            onClick={() => handleOnViewAll()}
          >
            View All
          </Typography>
        }
      >
        <Chart options={{ ...chart }} series={chart.series} type='bar' width={'100%'} height={250} />
      </DashboardTile>
    </React.Fragment>
  )
}

AgingSummaryTile.displayName = 'DashboardTileComponent'

export default AgingSummaryTile
