import instance from './instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/c/sas')

export default class AgingSummaryServices {

  static getAccountList(params) {
    return axios({
      url: `/collection/agingsummary/accounts`,
      method: 'GET',
    })
  }

  static getClientSOAList(params) {
    return axios({
      url: `/collection/agingsummary/client/soa-list/${params.accountNumber}`,
      method: 'GET',
    })
  }

  static getClientSOADetails(params) {
    return axios({
      url: `/audit-trail/list/${params.reportType}`,
      method: 'GET',
      params
    })
  }

  static getSummaryBarData(params) {
    return axios({
      url: `/collection/agingsummary/summary-bar`,
      method: 'GET',
      params
    })
  }

}
