import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import moment from 'moment'

// MATERIAL UI
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import ViewListRoundedIcon from '@material-ui/icons/ViewListRounded'
import { withStyles } from '@material-ui/core/styles'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import Grid from '@material-ui/core/Grid'
import Checkbox from '@material-ui/core/Checkbox'

// SHARED COMPONENTS
import CustomEnhancedTable from '../../../Components/CustomEnhancedTable/CustomEnhancedTable'

// UTILITIES
import { CurrencyFormatter, DateFormatterByAbbreviation } from 'Utilities/TextFormatter'

import style from '../AgingSummaryClientPage.module.scss'

export default function ListView(props) {
  const {
    config,
    contents,
    count,
    list,
    onListViewFilterChange,
    params,
    setShowModal,
    fetchItemDetails,
    onCheckboxChanged,
    header,
    reportType,
    handlePregenerateCDF,
    handleInlinePregenerateCDF
  } = props

  const [checked, setChecked] = useState({})

  // LOCAL CONSTANTS
  const tableHeaders = config.header

  const StyledTableCell = withStyles((theme) => ({
    head: {
      backgroundColor: theme.palette.common.grey,
      color: theme.palette.common.white
    },
    body: {
      fontSize: 14
    }
  }))(TableCell)

  const StyledTableRow = withStyles((theme) => ({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
      }
    }
  }))(TableRow)

  const pagination = {
    count: list ? list.count : 0,
    rowsPerPage: list ? list.limit : 0,
    page: list ? list.page : 0
  }

  // LOCAL METHODS
  const handleViewLogDetail = (params) => {
    setShowModal(true)
    fetchItemDetails(params)
  }

  const changeView = params => {
    if (onListViewFilterChange) {
      onListViewFilterChange(params)
    }
  }


  return (
    <CustomEnhancedTable
      count={count}
      changeView={changeView}
      tableHeaders={tableHeaders}
      pagination={pagination}
      textBoxHeight={40}
      search
      sort
      fetchData={onListViewFilterChange}
      params={params}
      hasLink
      hasCheckbox
    >
      <TableBody>
        {contents && contents.map((row, contentsIndex) => (
          <StyledTableRow key={row._id}>
            <StyledTableCell component='th'>
              <Checkbox
                key={row.soa_number}
                onChange={(e) => {
                  const selected = { soa_number: row.soa_number, value: e.target.checked }
                  setChecked(i => ({ ...i, [row.soa_number]: e.target.checked }))
                  onCheckboxChanged(selected)
                }}
                checked={checked[row.soa_number]}
                style={{ color: "rgba(0, 0, 0, 0.54)" }} />
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'>
                {row.soa_date}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'>
                <Typography variant='subtitle2' align='left'
                  className={style.linkTypeText}>
                  {row.soa_number}
                </Typography>
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'>
                {row.due_date}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'>
                {row.days_overdue}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'>
                {row.total_amount_due}
              </Typography>
            </StyledTableCell>
            <StyledTableCell component='th'>
              <Typography variant='subtitle2' align='left'
                onClick={i => {
                  handleInlinePregenerateCDF(row.soa_number)
                }}
                className={style.linkTypeText}>
                Create CD
              </Typography>
            </StyledTableCell>
          </StyledTableRow>
        ))}
      </TableBody>
    </CustomEnhancedTable>
  )
}
